@import "@/assets/global.scss";
















































































































.slot-span {
  position: absolute;
  top: 50%;
  right: 14px;
  transform: translate(0, -50%);
}

.header {
  position: fixed;
  z-index: 10;
  top: 52px;
  width: 100%;
  height: 104px;
  padding: 0 16px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #E9E9E8;
  background-color: #FAFAFA;

  > div {
    width: 100%;
    height: 56px;
    padding: 16px 8px;
    background: #F2F2F2;
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;

    > img {
      margin-right: 4px;
    }
  }
}

.main-content {
  padding-top: 156px;
  margin-top: 24px;
  box-sizing: border-box;

  > div {
    margin-bottom: 24px;
  }
}

.btn-group {
  display: flex;
  justify-content: space-between;

  > .btn {
    width: 145px;
    height: 46px;
    padding: 11px 0;
  }

  > .btn:first-child {
    background: #FFFFFF;
    border: 1px solid #E9E9E8;
    box-sizing: border-box;
    border-radius: 6px;
  }

  > .btn:last-child {
    background: #FCA311;
    border-radius: 6px;
  }
}

.slide-leave-active,
.slide-enter-active {
  transition-duration: 0.3s;
  transition-timing-function: ease;
}

.slide-enter-to, .slide-leave {
  max-height: 100px;
  overflow: hidden;
}

.slide-enter, .slide-leave-to {
  overflow: hidden;
  max-height: 0;
}

.search-icon {
  height: 18px;
  width: 18px;
  background-image: url('../../assets/img/search.svg');
}
